<template>
<div>
    apitest
</div>
</template>

<script type="text/javascript">
import Vue from 'vue'
import VueResource from 'vue-resource'

Vue.use(VueResource)
// Vue.http.options.emulateJSON = true;
window.file = new File([new Blob(['xxxx'], {type: 'text/html'})], 'name.txt')
export default {
  name: 'apitest',
  created () {
    console.log(this.$route)
    Vue.http.get('/testapi/api/v1/session.php', {
      params: {
        name: 'xxxx'
      }
    }).then(res => {
      console.log('res: ', res)
    }).catch(err => {
      console.log(err)
    })


    Vue.http.post('/testapi/api/v1/session.php', {
      userid: '123',
      blob: window.file
    }, {
      params: {
        uid: 1001,
        data: [12, 34, 567, 23]
      },
      emulateJSON: true
    }).then(res => {
      console.log('res: ', res)
    }).catch(err => {
      console.log(err)
    })

    let formData = new FormData()
    formData.append('file', window.file)
    formData.append('data', {
      userid: '123',
      blob: window.file
    })
    Vue.http.post('/testapi/api/v1/session.php', formData, {
      params: {
        uid: 1002,
        data: [12, 34, 567, 23]
      },
      emulateJSON: true
    }).then(res => {
      console.log('res: ', res)
    }).catch(err => {
      console.log(err)
    })

    Vue.http.post('http://localhost/api/v1/cross-domain.php', formData, {
      params: {
        uid: 1002,
        data: [12, 34, 567, 23]
      },
      emulateJSON: true
    }).then(res => {
      console.log('res: ', res)
    }).catch(err => {
      console.log(err)
    })

    let uri = 'http://120.24.7.22:8081/customer_plat/api/machine.MachineGourpAction/addGourp?token=5bef8046-639b-4cef-89fc-3c29d258db0a'
    Vue.http.post(uri, {gourp_name: '测试组'}, {emulateJSON: true}).then(res => {
      console.log(res)
    })

  }
}
</script>



